const app = getApp()
export default {
	//这是优惠卷的组件
	data() {
		return {
			tokey:"",
			list: [
					{
						price: 3,
						preferential: 58
					},
					{
						price: 5,
						preferential: 88
					},
					{
						price: 10,
						preferential: 168
					}
				,
				{
						price: 18,
						preferential: 68
					},
					{
						price: 20,
						preferential: 78
					},
					{
						price: 10,
						preferential: 168
					},
				
				{
						price: 30,
						preferential: 98
					},
					{
						price: 11,
						preferential: 108
					},
					{
						price: 13,
						preferential: 208
					}
				
			],
			couponsbool:true
		}
	},
	methods:{
		Platformreceive(cid){
			// console.log(cid)
			uni.request({
				url:`${app.globalData.Requestpath}activity/userGetPlatformCoupon`,
				method:"POST",
				data:{
					token:this.tokey,
					cid:cid
				},
				success(res) {
					if(res.data.code==0){
						app.globalData.showtoastsame(res.data.msg)
					}else{
						app.globalData.showtoastsame(res.data.msg)
					}
				}
			})
		}
	},
	created() {
		const _this = this
		uni.getStorage({
			key:"bindtokey",
			success(res){
				_this.tokey = res.data
				// console.log(res.data)
				uni.request({
					url:`${app.globalData.Requestpath}activity/getPlatformCouponTypeList`,
					method:"POST",
					data:{
						token:res.data,
						page:1,
						pageSize:3,
					},
					success(rescoupons) {
						if(rescoupons.data.code==0){
							_this.list = rescoupons.data.data.list
							_this.couponsbool = true
						}else{
							_this.couponsbool = false
						}
					}
				})
			}
		})
	}
}
